<template>
  <v-container :class="{'ml-10':!isMobile}" :style="{ width: isMobile ? '100%' : '50%'}" class="py-0 px-4" fluid>
    <title-bar :title="title"/>
    <background-image :src="backgroundImagePath"/>
    <v-btn
        color="primary"
        fab
        fixed
        right
        style="bottom: 68px"
        @click="()=>{}"
    >
      <v-icon>
        mdi-plus
      </v-icon>
    </v-btn>
    <v-row no-gutters>
      <v-col
          cols="6"
          class="pr-3"
      >
        <v-menu
            transition="scale-transition"
            offset-y
            max-width="290px"
            min-width="auto"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
                dense
                v-model="query.monthString"
                label="请选择月份"
                prepend-icon="mdi-calendar"
                readonly
                v-bind="attrs"
                v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker
              v-model="query.monthString"
              type="month"
              no-title
              color="primary"
              locale="zh-cn"
              scrollable
          >
          </v-date-picker>
        </v-menu>
      </v-col>
      <v-col
          cols="6"
          class="pl-3"
      >
        <v-text-field
            dense
            v-model="query.description"
            label="请输入关键字"
            clearable
            prepend-icon="mdi-magnify"
        ></v-text-field>
      </v-col>
    </v-row>
    <v-card
        class="pa-0 rounded-lg"
        flat fluid
    >
      <v-card-subtitle class="pa-3 pb-1 font-weight-medium">总支出</v-card-subtitle>
      <v-card-title :style="{ color: lightPrimary }" class="px-3 py-0 text-h3"
                    v-text="numFormat(billStat.expenditure)"></v-card-title>
      <v-card-subtitle class="pa-3 pt-4">
        <v-row no-gutters>
          <v-col cols="6">
            <span class="font-weight-medium">总收入</span>
            <span class="ml-3" v-text="'¥'+numFormat(billStat.income)"/>
          </v-col>
          <v-col cols="6">
            <span class="font-weight-medium">结余</span>
            <span class="ml-3" v-text="'¥'+numFormat(billStat.balance)"/>
          </v-col>
        </v-row>
      </v-card-subtitle>
    </v-card>
    <v-skeleton-loader
        v-if="query.loading"
        v-for="(item, i) in 6"
        class="rounded-lg mt-5"
        type="list-item-avatar-two-line"
    />
    <v-container v-for="(date, i) in billInfoList"
                 :key="i"
                 class="pa-0">
      <v-row align="center" class="px-0 py-3 font-weight-medium d-flex justify-space-between" no-gutters
             style="position: relative"
      >
        <span class="text-subtitle-2 text--darken-1 grey--text" v-text="date.billDate"></span>
        <span class="text-subtitle-2 text--darken-1 grey--text">
          <span>余额</span>
          <span class="ml-2" v-text="'¥'+numFormat(date.amount)"/>
        </span>
      </v-row>
      <v-card
          class="py-0  px-3 rounded-lg"
          flat fluid
      >
        <v-container v-for="(item, i) in date.list" :key="i" class="pa-0">
          <v-row v-ripple align="center" no-gutters style="height: 60px;cursor: pointer"
                 @click="()=>{}">
            <v-col cols="1">
              <v-btn color="primary" depressed fab x-small>
                <v-icon v-if="item.billType.icon">mdi-{{ item.billType.icon }}</v-icon>
                <v-icon v-else>mdi-credit-card-outline</v-icon>
              </v-btn>
            </v-col>
            <v-col class="ml-3">
              <div class="text-subtitle-1" v-text="item.billType.billTypeName"/>
              <div class="text-subtitle-2 text--darken-1 grey--text" v-text="item.description"/>
            </v-col>
            <v-col class="d-flex justify-end" cols="3">
              <div class="text-subtitle-1">
                <span v-text="'¥'+numFormat(item.amount)"/>
              </div>
            </v-col>
            <v-col class="d-flex justify-end" cols="1">
              <v-icon>mdi-chevron-right</v-icon>
            </v-col>
          </v-row>
          <v-divider v-if="i!==(date.list.length-1)"/>
        </v-container>
      </v-card>
    </v-container>
    <v-dialog v-model="showDialog" max-width="400px" persistent>
      <v-card>
        <v-form ref="form" v-model="formValid">
          <v-container class="pa-0" fluid>
            <v-row justify="center" no-gutters class="mt-6">
              <v-col cols="12">
                <v-row no-gutters justify="space-around">
                  <v-col cols="5">
                    <v-text-field
                        label="金额"
                        prefix="¥"
                        :rules="[(value) => !!value || '请输入金额', rules.isFloat]"
                        v-model="selectedBill.amount"
                        clearable
                    />
                  </v-col>
                  <v-col cols="5">
                    <v-combobox
                        :items="selectedBill.flow === 'in' ? incomeTypeList : outcomeTypeList"
                        v-model="selectedBill.type"
                        :rules="[(value) => !!value || '请选择类型']"
                        label="类型"
                        clearable
                    />
                  </v-col>
                </v-row>
                <v-row justify="space-around" no-gutters>
                  <v-col cols="5">
                    <v-dialog
                        persistent
                        ref="dialog"
                        v-model="showDatePicker"
                        :return-value.sync="selectedBill.billDate"
                        width="290px"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                            v-model="selectedBill.billDate"
                            label="日期"
                            :rules="[(value) => !!value || '请输入日期']"
                            readonly
                            v-bind="attrs"
                            v-on="on"
                        />
                      </template>
                      <v-date-picker
                          v-model="selectedBill.billDate"
                          scrollable
                          locale="zh-cn"
                      >
                        <v-spacer/>
                        <v-btn
                            color="grey darken-1"
                            text
                            @click="showDatePicker = false"
                            v-text="'取消'"
                        />
                        <v-btn
                            text
                            color="primary"
                            @click="$refs.dialog.save(selectedBill.billDate)"
                            v-text="'确认'"
                        />
                      </v-date-picker>
                    </v-dialog>
                  </v-col>
                </v-row>
                <v-row no-gutters justify="center">
                  <v-col cols="11">
                    <v-textarea
                        v-model="selectedBill.description"
                        auto-grow
                        rows="1"
                        clearable
                        label="备注"
                    />
                  </v-col>
                </v-row>
                <v-row no-gutters>
                  <v-spacer/>
                  <v-btn
                      color="grey darken-1"
                      text
                      @click="showDialog = false"
                      v-text="'取消'"
                  />
                  <v-btn
                      text
                      color="blue darken-1"
                      @click="updateBill"
                      v-text="'更新'"
                  />
                  <v-btn
                      text
                      color="red darken-1"
                      @click="removeBill"
                      v-text="'删除'"
                  />
                </v-row>
              </v-col>
            </v-row>
          </v-container>
        </v-form>
      </v-card>
    </v-dialog>
    <v-container class="py-16"/>
  </v-container>
</template>

<script>
import BackgroundImage from '@/pages/Index/components/BackgroundImage'
import TitleBar from "@/pages/Index/components/TitleBar";

export default {
  name: "Home",
  components: {BackgroundImage, TitleBar},
  computed: {
    isMobile: function () {
      return this.$vuetify.breakpoint.mobile;
    },
    isDark: function () {
      return this.$vuetify.theme.dark;
    },
    lightPrimary: function () {
      return this.$vuetify.theme.themes.light.primary;
    }
  },
  data: function () {
    return {
      title: '账本',
      backgroundImagePath: this.GLOBAL.images.ideas,
      query: {
        loading: true,
        monthString: new Date().Format("yyyy-MM"),
        description: null
      },
      billInfoList: [],
      billStat: {
        expenditure: 0.00,
        income: 0.00,
        balance: 0.00
      },
      formValid: false,
      showDialog: false,
      showDatePicker: false,
      outcomeTypeList: [
        "餐饮",
        "水果零食",
        "购物",
        "住房",
        "生活服务",
        "通讯",
        "生活日用",
        "人情社交",
        "穿搭美容",
        "医疗保健",
        "休闲娱乐",
        "家居家电",
        "网络虚拟",
        "其他",
      ],
      incomeTypeList: [
        "工资",
        "奖金补贴",
        "生活费",
        "人情社交",
        "兼职",
        "投资理财",
        "报销",
        "中奖",
        "其他",
      ],
      selectedBill: {},
      rules: {
        isFloat: this.GLOBAL.rules.isFloat,
      },
    };
  },
  watch: {
    query: {
      handler(newVal, oldVal) {
        console.log(newVal);
        this.listBill();
      },
      deep: true
    }
  },
  methods: {
    selectBill(item) {
      this.showDialog = true;
      this.selectedBill = item;
    },
    listBill() {
      this.axios.post("/bill/listUserBill", JSON.stringify(this.query)).then((response) => {
        this.billInfoList = response.data.data.billInfoList;
        this.billStat = response.data.data.billStat;
        this.query.loading = false;
      });
    },
    removeBill() {
      this.axios.delete("/bill/removeBill/" + this.selectedBill.id)
          .then(() => {
            this.showDialog = false;
            this.$notify({
              title: "删除成功",
              message: null,
              type: "success",
              duration: 2000,
            });
            this.axios.get("/bill/listUserBill").then((response) => {
              this.billList = response.data.data;
            });
          });
    },
    updateBill() {
      if (this.$refs.form.validate()) {
        this.axios.put("/bill/updateBill", JSON.stringify(this.selectedBill))
            .then(() => {
              this.showDialog = false;
              this.$notify({
                title: "更新成功",
                message: null,
                type: "success",
                duration: 2000,
              });
            });
      }
    },
    numFormat(number) {
      return number.toString().replace(/\d+/, function (n) { // 先提取整数部分
        return n.replace(/(\d)(?=(\d{3})+$)/g, function ($1) {
          return $1 + ",";
        });
      });
    }
  },
  mounted() {
    this.$emit("changeTitle", this.title);
    this.listBill();
  }
};
</script>

